html {
  scroll-behavior: smooth;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.icono {
  font-size: 60px;
}

.iconofacebook {
  color: rgb(73, 73, 182);
  transition: color .3s;
}

.iconofacebook:hover {
  color: white;
  transition: color .2s;
}

.iconoWsp:hover {
  color: white;
  transition: color .2s;
}

.object {
  animation: MoveUpDown 1s linear infinite;
  position: absolute;
  left: 49%;
  bottom: 0;
  margin-bottom: 20px;
  font-size: 50px;
  color: white;
}

@keyframes MoveUpDown {
  0%, 100% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-20px);
  }
}

